.navbar {
  width: 100%;
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;
  justify-content: space-between;
  color: whitesmoke;
  background-color: #222326;
  padding: 8px;
}

.title {
  font-weight: 800;
  margin: 6px 0 5px 0;
}

.links {
  display: flex;
  padding-right: 15px;
  margin: 5px 0 5px 0;
}

.logo {
  display: flex;
}

.logo-img {
  padding-right: 10px;
  height: 60px;
}

li {
  display: block;
  padding: 0.5rem 1rem;
}

a {
  color: whitesmoke !important;
  text-decoration: none;
}

a:hover {
  color: #66d68e !important;
}

.main-logo {
  margin: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 10%;
}

.homepage {
  display: flex;
}

.readme {
  color: #22ca5d !important;
}

h1 {
  text-align: center;
}

.main-text {
  margin: auto;
  width: 85%;
}

.input {
  margin-bottom: 10px;
}

.text-input {
  width: 35% !important;
}

.submit-img {
  height: 30px;
}

.submit {
  margin-left: 4px;
}

.main-content {
  display: flex;
  justify-content: space-between;
}

.column {
  width: 48%;
}

.column-30 {
  width: 30%;
}

.image-container {
  position: relative;
  display: inline-block;
}

.image-container img {
  width: 80px;
  height: auto;
  transition: opacity 0.3s; /* Smooth transition for opacity */
  border-radius: 3px;
  margin: 0 8px;
}

.image-container button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 8px;
  width: 80px;
  height: 80px;
  color: #22ca5d;
  border: none;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s; /* Smooth transition for opacity */
}

.image-container:hover img {
  opacity: 0.7;
}

.image-container:hover button {
  opacity: 1;
  color: #22ca5d;
}

hr {
  border-top: 3px solid #ababab !important;
  width: 95%;
}

.modal-li {
  padding: 0.2rem;
  display: list-item;
}

.modal-body {
  display: flex;
}

.uri-tooltip {
  text-decoration: underline;
}

.uri-tooltip:hover {
  color: #66d68e !important;
}

.result {
  display: flex;
  flex-direction: row;
  align-items: center;
